<script>
import { BRow, BCol, BButton } from 'bootstrap-vue'

import StatisticCardVertical from '@core/components/statistics-cards/StatisticCardVertical.vue'
import StatisticCardHorizontal from '@core/components/statistics-cards/StatisticCardHorizontal.vue'
import { getUserData } from '@/auth/utils'
import EcommerceMedal from './AnalyticsMedal.vue'
import EcommerceStatistics from './AnalyticsStatistics.vue'
import EcommerceRevenueReport from './AnalyticsRevenueReport.vue'
import EcommerceOrderChart from './AnalyticsOrderChart.vue'
import EcommerceProfitChart from './AnalyticsProfitChart.vue'
import EcommerceEarningsChart from './AnalyticsEarningsChart.vue'
import EcommerceCompanyTable from './AnalyticsCompanyTable.vue'
import EcommerceMeetup from './AnalyticsMeetup.vue'
import EcommerceBrowserStates from './AnalyticsBrowserStates.vue'
import EcommerceGoalOverview from './AnalyticsGoalOverview.vue'
import EcommerceTransactions from './AnalyticsTransactions.vue'
import CardAnalyticRevenueReport from '@/views/card/card-analytic/CardAnalyticRevenueReport.vue'
import CardAnalyticRevenue from '@/views/card/card-analytic/CardAnalyticRevenue.vue'

export default {
  components: {
    BRow,
    BCol,
    BButton,
    EcommerceMedal,
    EcommerceStatistics,
    EcommerceRevenueReport,
    EcommerceOrderChart,
    EcommerceProfitChart,
    EcommerceEarningsChart,
    EcommerceCompanyTable,
    EcommerceMeetup,
    EcommerceBrowserStates,
    EcommerceGoalOverview,
    EcommerceTransactions,
    StatisticCardVertical,
    StatisticCardHorizontal,
    CardAnalyticRevenueReport,
    CardAnalyticRevenue,
  },
  data() {
    return {
      data: {},
    }
  },
  created() {
    // data
    this.$http.get('/analytics/data')
      .then(response => {
        this.data = response.data

        // ? Your API will return name of logged in user or you might just directly get name of logged in user
        // ? This is just for demo purpose
        const userData = getUserData()
        this.data.congratulations.name = userData.fullName.split(' ')[0] || userData.username
      })
    // Subscribers gained
    this.$http.get('/card/card-statistics/subscribers')
      .then(response => {
        this.subscribersGained = response.data
      })

    // Revenue Generated
    this.$http.get('/card/card-statistics/revenue')
      .then(response => {
        this.revenueGenerated = response.data
      })

    // Sales
    this.$http.get('/card/card-statistics/sales')
      .then(response => {
        this.quarterlySales = response.data
      })

    // Orders
    this.$http.get('/card/card-statistics/orders')
      .then(response => {
        this.ordersRecevied = response.data
      })

    // Site Traffic gained
    this.$http.get('/card/card-statistics/site-traffic')
      .then(response => {
        this.siteTraffic = response.data
      })

    // Active Users
    this.$http.get('/card/card-statistics/active-users')
      .then(response => {
        this.activeUsers = response.data
      })

    // Newsletter
    this.$http.get('/card/card-statistics/newsletter')
      .then(response => {
        this.newsletter = response.data
      })
  },
}
</script>

